html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:after, *:before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  display: block;
}

body {
  margin: 0;
}

.main {
  margin: 0;
  background-color: hsl(30deg, 38%, 92%);
  font-size: 1.4rem;
  font-family: "Montserrat", sans-serif;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  max-height: 95%;
  width: 90%;
  max-width: 55rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
@media (min-width: 500px) {
  .card {
    flex-direction: row;
  }
}
.card__image {
  background-image: url(../img/image-product-mobile.jpg);
  background-size: cover;
  width: 100%;
  height: 30rem;
}
@media (min-width: 500px) {
  .card__image {
    background-image: url(../img/image-product-desktop.jpg);
    height: 40rem;
    width: 100rem;
  }
}

.content {
  padding: 2rem;
}
@media (min-width: 500px) {
  .content {
    padding: 2.5rem;
  }
}
.content__paragraph {
  margin: 0;
  color: hsl(228deg, 12%, 48%);
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  letter-spacing: 0.4rem;
}
.content__heading {
  font-family: "Fraunces", serif;
  font-size: 2.8rem;
  line-height: 1;
  margin: 2rem 0 1.5rem 0;
}
.content__text {
  font-size: 13px;
  line-height: 1.6;
  color: hsl(228deg, 12%, 48%);
  margin: 0;
}

.price {
  display: flex;
  align-items: center;
  margin-block: 3.5rem 1.5rem;
}
.price__first {
  color: hsl(158deg, 36%, 37%);
  font-size: 3rem;
  font-family: "Fraunces", serif;
  margin: 0 2rem 0 0;
}
.price__second {
  text-decoration: line-through;
  color: hsl(228deg, 12%, 48%);
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(158deg, 36%, 37%);
  border: none;
  width: 100%;
  color: hsl(0deg, 0%, 100%);
  padding: 1.2rem;
  border-radius: 0.7rem;
  font-size: 14px;
  transition: transform 0.3s;
}
.btn__image {
  margin-right: 1rem;
}
.btn:hover {
  cursor: pointer;
  transform: scale(1.1);
}